.dublicate-box{
    width: 40%;
    height: 4vw;
    margin-top: 10px;
    box-shadow: 0px 0px 4px 1px rgba(0,0,0,0.75);
    border-radius: 5px;  
    background-color: red;
    position: fixed;
    top: 4%;
    left: 55%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.dublicate-txt{
    color: white;
    font-size: 2vw;
    font-weight: bold;
    margin: 0;
    padding: 0;
}